<script>
import axios from 'axios'
export default {
  name: 'home',
  data: () => ({
     naver: false,
    kakao: false,
    selectItems: ['', '', ''],
    alert:false,
   dialog: false,
   dialog1: false,
   contentHtml: '',
    contents: {
      main1: '',
      main2: '',
      check1: '',
      who1: '',
      who2: '',
      where1: '',
      where2: '',
      how1: '',
      how2: '',
      how3: '',
      eat1: '',
      eat2: '',
      eat3: '',
      delivery1: '',
      delivery2: '',
      jido1: '',
      title: '',
      subtitle: '',
      main_story: '',
      who_title1: '',
      who_title2: '',
      who_subtitle: '',
      who_story: '',
      where_title: '',
      where_story: '',
      how_title1: '',
      how_story1: '',
      how_title2: '',
      how_story2: '',
      eat_title: '',
      eat_story: '',
      delivery_title: '',
      delivery_story: '',
      product_info_name: '',
      product_info_gusung: '',
      product_info_maker: '',
      product_info_wonsanji: '',
      product_info_wonjae: '',
      product_info_bogwan1: '',
      product_info_bogwan2: '',
      jido_title: '',
      jido_story: '',
      text: ''
    }
  }),
  created () {
    setInterval(() => {
      localStorage.setItem('temphtml',this.contents);
    }, 60000)
    this.id = this.$route.params.id;
    axios.get(`https://todo.bagcw.com/nospo/detail/${this.id}`).then( (res) => {
      console.log(res.data[0])
      if(res) {
        this.contents = res.data[0]
      }
    })
  },
  methods: {
    selectPlaform (e) {
      if (e === '') {
        this.naver = true
        this.kakao = false
        this.jagalchi = false
      }
      if (e === '') {
        this.kakao = true
        this.naver = false
        this.jagalchi = false
      }
      if (e === '') {
        this.kakao = false
        this.naver = false
        this.jagalchi = true
      }
    },
    commandExec (commandName, value) {
      document.execCommand(commandName, false, value)
    },
    tempSave () {
      let contents = JSON.stringify(this.contents)
      localStorage.setItem('temphtml', contents)
    },
    htmlSave () {
      let contents = this.dataTransfer();
      console.log(contents)
      axios.put('https://todo.bagcw.com/nospo/detail', contents).then( (res) => {
        if(res) {
          this.alert = true
          setTimeout(() => {
            this.alert = false
          }, 3000)
        }
      })
    },
    dataTransfer () {
      this.contents.title = document.querySelector('#title').innerHTML
      this.contents.subtitle = document.querySelector('#subtitle').innerHTML
      this.contents.main_story = document.querySelector('#main_story').innerHTML
      this.contents.who_title1 = document.querySelector('#who_title1').innerHTML
      this.contents.who_title2 = document.querySelector('#who_title2').innerHTML
      this.contents.who_subtitle = document.querySelector('#who_subtitle').innerHTML
